import { createConfirmDialog } from 'vuejs-confirm-dialog'
import VConfirm from '@/components/VConfirm.vue'

type CallbackType = () => Promise<void> | void;

export interface IConfirmOptions {
  title?: string;
  message?: string;
  confirmText?: string;
  cancelText?: string;
  onConfirm: CallbackType;
  onCancel?: CallbackType;
}

const useConfirm = (options: IConfirmOptions) => {
  const dialog = createConfirmDialog(VConfirm, {
    title: options.title || null,
    message: options.message || null,
    confirmText: options.confirmText || null,
    cancelText: options.cancelText || null
  })

  dialog.onConfirm(options.onConfirm)

  if (options.onCancel) {
    dialog.onCancel(options.onCancel)
  }

  return dialog
}

export default useConfirm
