
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import type { Header, Item, ServerOptions } from 'vue3-easy-data-table'
import { ICustomerGroupItem, ICustomerTableItem, IFilter, IResponseTable } from '@/types'
import PageCard from '@/components/PageCard.vue'
import useFetching, { FetchStatus } from '@/hooks/useFetching'
import useConfirm from '@/hooks/useConfirm'
import VFilter from '@/components/VFilter.vue'

export interface IData {
  headers: Header[];
  itemsSelected: Item[];
  serverOptions: ServerOptions;
  filterModel: object;
}

export default defineComponent({
  setup () {
    const store = useStore()
    const [loadTable, status, error] = useFetching((payload) => {
      return store.dispatch('customer/loadTable', payload)
    })

    const [deleteRows, deleteStatus, deleteError] = useFetching((ids: number[]) => {
      return store.dispatch('customer/deleteRows', ids)
    })

    return {
      loadTable,
      status,
      error,
      deleteRows,
      deleteStatus,
      deleteError
    }
  },

  components: {
    VFilter,
    PageCard
  },

  computed: {
    isLoading (): boolean {
      return this.status === FetchStatus.pending
    },

    dataTable (): IResponseTable<ICustomerTableItem> {
      return this.$store.getters['customer/getDataTable']
    },

    customerGroups (): ICustomerGroupItem[] {
      return this.$store.getters['customerGroup/getItems'] || []
    },

    filters (): IFilter[] {
      const items = this.customerGroups.map((g) => ({ id: g.id, name: g.name }))

      return [
        { name: 'Имя клиента', key: 'firstName' },
        { name: 'Email', key: 'email' },
        { name: 'Группа клиента', key: 'customerGroupId', type: 'select', items: items },
        { name: 'Статус', key: 'isActive', type: 'select', items: [{ name: 'true', id: true }, { name: 'false', id: false }] },
        { name: 'Ip', key: 'ip' },
        { name: 'Дата добавления', key: 'createdAt', type: 'date' }
      ]
    }
  },

  watch: {
    serverOptions: {
      handler: function (value) {
        this.loadServerSide(value)
      },
      deep: true
    }
  },

  data (): IData {
    return {
      headers: [
        { text: 'id', value: 'id', sortable: true },
        { text: 'Имя клиента', value: 'firstName', sortable: true },
        { text: 'E-mail', value: 'email', sortable: true },
        { text: 'Группа клиента', value: 'customerGroupId', sortable: true },
        { text: 'Статус', value: 'isActive', sortable: true },
        { text: 'IP', value: 'ip', sortable: true },
        { text: 'Дата регистрации', value: 'createdAt', sortable: true },
        { text: 'Действие', value: 'action' }
      ],

      itemsSelected: [],

      serverOptions: {
        page: 1,
        rowsPerPage: 5,
        sortBy: 'id',
        sortType: 'desc'
      },

      filterModel: {
        firstName: null,
        email: null,
        customerGroupId: null,
        isActive: null,
        ip: null,
        createdAt: null
      }
    }
  },

  mounted () {
    this.loadServerSide(this.serverOptions)
  },

  methods: {
    handleCreate () {
      this.$router.push({ name: 'customer-create' })
    },

    handleUpdate (item: ICustomerTableItem) {
      this.$router.push({ name: 'customer-update', params: { id: item.id } })
    },

    async handleDelete () {
      const dialog = useConfirm({
        title: 'Удаление клиентов',
        message: 'Вы действительно хотите удалить выбранных клиентов?',
        onConfirm: async () => {
          await this.deleteRows(this.itemsSelected.map((item) => item.id))
          this.itemsSelected = []
          await this.loadServerSide(this.serverOptions)
        }
      })

      await dialog.reveal()
    },

    loadServerSide (options: ServerOptions) {
      const payload: any = {
        limit: options.rowsPerPage,
        offset: (options.page - 1) * options.rowsPerPage
      }

      if (options.sortBy && options.sortType) {
        payload.order = `${options.sortBy}:${options.sortType}`
      }

      this.loadTable(payload)
    }
  }
})
